import axios from 'axios';
import React, { useEffect, useState } from 'react';
import PercentChange from './PercentChange';
import TableFilters from './TableFilters';


const HeaderInfos = () => {

    const [headerData, setHeaderData] = useState([]);
    const [headerDataDominance, setHeaderDataDominance] = useState([]);

    useEffect(() => {
        axios.get("https://api.coingecko.com/api/v3/global")
        .then((res) => {
            setHeaderData(res.data.data);
            setHeaderDataDominance(res.data.data.market_cap_percentage);
        });
    }, []);
    
    return (
            <div className='header-container'>
                <ul className='title'>
                    <li>
                        <h1><img src='./assets/logo.png' alt='logo'/>
                        CRYPTO VISION</h1>
                    </li>
                    <li>
                        Crypto-monnaies : {" "} {headerData.active_cryptocurrencies && headerData.active_cryptocurrencies.toLocaleString()}
                    </li>
                    <li>
                        Marchés : {" "} {headerData.markets && headerData.markets.toLocaleString()}
                    </li>
                </ul>
                <ul className="infos-mkt">
                    <li className="global-mkt">
                        Global Market Cap : <PercentChange percent={headerData.market_cap_change_percentage_24h_usd}/>
                    </li>
                    <li>
                        BTC dominance : {""} {headerDataDominance.btc && headerDataDominance.btc.toFixed(1) + "%"}
                    </li>
                    <li>
                        ETH dominance : {""} {headerDataDominance.eth && headerDataDominance.eth.toFixed(1) + "%"}
                    </li>
                </ul>
            <TableFilters/>
            </div>
    );
};

export default HeaderInfos;